body {
    background: $bg_body;
}


.quote__block__wrapper {
    background: $beige;
    padding: 50px 0;

    .quote__block {
        display: block;
        max-width: 1000px;
        margin: 0 auto;

        .flex {
            gap: 20px;
            margin-top: 20px;
        }

        .video {
            flex-basis: 40%;
            width: 100%;
            height: 250px;
        }

        .text {
            flex-basis: 50%;
            font-size: 38px;
        }
    }
}

.text-with-image-block {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .content {
        flex-basis: 50%;
        display: flex;
        flex-direction: column;
        gap: 30px !important;
        padding: 40px;

        .text {
            display: flex;
            flex-direction: column;
            gap: 15px;
        }
    }

    .image {
        flex-basis: 50%;
        display: block;
        width: 100%;
        height: 100%;
        flex: 1 1 50%;
    }
}

.staff-slider {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .staffSlider {
        width: 100%;

        .swiper-slide {
            height: 400px;
        }
    }
}

.products__wrapper {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .swiper-slide {
        height: 400px;
    }
}

.founder__block {
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .content {
        flex-basis: 65%;
        padding: 40px;

        h2 {
            margin-bottom: 40px;
        }

        .line {
            background-color: $red;
            height: 1px;
            width: 100px;
            margin: 15px 0;
        }

        .founder__about {
            opacity: .7;
        }

        .text {
            display: flex;
            flex-direction: column;
            gap: 20px;
            margin-top: 30px;
        }
    }

    .image {
        flex-basis: 35%;
    }
}

.equipment__block {
    margin-top: 100px;

    .title-with-slider-button {
        margin-bottom: 30px;
    }

    .equipmentSlider {
        .swiper-slide {
            background-color: $beige;
            padding: 40px;
            display: flex;

            .content {
                flex-basis: 50%;
                display: flex;
                flex-direction: column;
                gap: 20px;

            }
        }
    }
}

.about-salon {
    margin-top: 100px;

    h2 {
        margin-bottom: 30px;
    }

    .grid-block {
        margin-bottom: 30px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-gap: 30px;

        .item {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
    }

    .image__block {
        width: 100%;
        height: 500px;
    }
}

.results__block {
    margin-top: 100px;

    .title-with-slider-button {
        margin-bottom: 30px;
    }

    .resultsSlider {
        .swiper-slide {
            height: 300px;
        }
    }
}

.feedbacks-slider {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .feedbacksSlider {
        .swiper-slide {
            min-height: 400px;
            padding: 20px;
            background-color: $beige;
            display: flex;
            flex-direction: column;
            gap: 20px;

            p {
                flex: 1;
                line-height: 135%;
            }

            .name {
                font-family: 'Athelas-Bold';
                font-weight: bold;
                font-size: 24px;
            }
        }
    }
}

.map__block{
    margin-top: 100px;
    h2{
        margin-bottom: 30px;
    }
    .map-with-block{
        position: relative;
        width: 100%;
        .info__block__wrapper{
            position: absolute;
            width: 80%;
            top: 10%;
            margin: 0 auto;
            display: flex;
            justify-content: end;
            .info__block{


                background-color: $bg_body;
                z-index: 999;
                height: 400px;
                width: 350px;
            }
        }
    }
}