@import '../vars.scss';

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Athelas-Bold';
    font-weight: bold;
}

.paddings {
    padding: 0 40px;
}

h1 {}

h2 {
    font-size: 44px;
    font-weight: 700;
    text-transform: uppercase;
}

h3 {
    font-size: 34px;
}
h4 {
    font-size: 28px;
}

.background{
    background-color: cadetblue;
}

.button-red-border {
    a {
        display: inline-block;
        padding: 20px;
        border: 2px solid $red;
        color: $black;
    }
}

.swiper {
    width: 100% !important;
}

.slider-buttons {
    display: flex;
    gap: 15px;

    div {
        margin-top: 0;
        position: static !important;
        border: 2px solid $red;
        height: 50px;
        width: 50px;
        color: $red;
    }
}

.underslider-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title-with-slider-button{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-with-red-point{
    display: flex;
    flex-direction: column;
    gap: 15px;
    li{
        position: relative;
        margin-left: 15px;
        &::after{
            content: '';
            position: absolute;
            left: -15px;
            top: 5px;
            background-color: $red;
            height: 5px;
            width: 5px;
            border-radius: 5px;

        }
    }
}