.footer{
    margin-top: 100px;
    .line{
        width: 100%;
        height: 2px;
        background-color: #4a4a4a;
    }
    .footer__content{
        display: flex;
        justify-content: space-between;
        margin-top: 15px;
        gap: 40px;
        .logo{
            flex-basis: 25%;
            background-color: cadetblue;
            height: 100%;
        }
        .grid__block{
            flex-basis: 70%;
            margin-top: 25px;
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-gap: 40px;
            .item{
                h4{
                    margin-bottom: 15px;
                }
                ul{
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    li{
                        a{
                            color: $black;
                        }
                    }
                    .mail{
                        margin-top: 15px;
                    }
                }
            }
        }
    }
    .footer-text{
        margin-top: 50px;
        .text{
            flex-basis: 60%;
        }
        a{
            color: $black;
        }
    }
}